import $ from 'jquery';

const geoplace = (() => {
	const placeField = $('.geoplace__field');
	const placePopup = $('.popup.geoplace__popup');
	const area = $('.geoplace__area');

	const openPopup = () => {
		placePopup.addClass('popup_show');
	};


	const closePopup = () => {
		placePopup.removeClass('popup_show');
	};


	placeField.on('keyup', () => {
		const el = $(this);
		console.log(el.val());
		el.val() !== '' ? openPopup() : closePopup();
	});

	placeField
  .keyup(function() {
    var value = $( this ).val();
    console.log(value);
  })
  .keyup();

})();

export default geoplace


